<template>
  <div class="login">
    <img src="../../assets/shopImage/left.png" alt="" class="left" @click="back">
    <header class="login_header">
      <img src="../../assets/shopImage/forget.png" alt="">
    </header>
    <section class="login_main">
      <label class="form">
        <img src="../../assets/shopImage/username.png" alt="">
        <input type="text" class="input" placeholder="请输入手机号" v-model.trim="username">
      </label>
      <label class="drag_slider">
        <slider @end="sliderEnd"></slider>
      </label>
      <label class="code" v-if="show">
        <a href="javascript:void(0)" @click="getCode" v-if="flag">获取验证码</a>
        <a href="javascript:void(0)"  v-if="!flag">{{time}}秒</a>
        <input type="text" class="input" placeholder="请输入验证码" v-model.trim="code">
      </label>
      <label class="form">
        <img src="../../assets/shopImage/password.png" alt="">
        <input type="password" class="input" placeholder="设置密码(6-18位字符)" v-model.trim="password">
      </label>
      <label class="form">
        <img src="../../assets/shopImage/password.png" alt="">
        <input type="password" class="input" placeholder="请再次输入密码" v-model.trim="password1">
      </label>
      <button class="login_btn" @click="login">完成</button><br>
    </section>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  import slider from './slider'
  export default{
    data(){
      return {
        flag: true,
        username: '',
        code: '',
        password: '',
        password1: '',
        time: 60,
        show:false
      }
    },
    components:{
      slider
    },
    methods:{
      sliderEnd(val){
        this.show = true;
      },
      getCode(){
        if (this.check.isPoneAvailable(this.username)) {
          //倒计时
          Indicator.open();
          this.flag = false;
          const timer = setInterval(() => {
            this.time --;
            if(this.time === 0) {
              this.flag = true;
              this.time = 60;
              clearInterval(timer);
            }
          }, 1000);

          this.post('home/Login/sendMsg',{
            tel: this.username
          }).then(res => {
            Indicator.close();
            if (res.code === 10000){
              Toast(res.data.msg)
            } else Toast(res.data.msg)
          })
        } else Toast('手机号格式错误')
      },
      //注册
      login(){
        //判断是否为空
        let isEquality = this.password === this.password1;
        let isCheck = this.check.isPoneAvailable(this.username) && this.check.isPassWordAvailable(this.password) && this.code;
        if (isCheck) {
          if (isEquality) {
            Indicator.open();
            this.post('home/Login/updatePassword', {
              username: this.username,
              msgCode: this.code,
              password: this.password,
              password1: this.password1,
            }).then(res => {
              Indicator.close();
              if (res.data.code === 10000) {
                let json = {
                  name: this.username,
                  word: this.password
                };
                localStorage.setItem('user', JSON.stringify(json));
                Toast(res.data.msg);
                this.$router.push('/submit')
              } else Toast(res.data.msg)
            })
          } else Toast('两次密码输入不一致');
        } else Toast('修改失败')
      },
      back(){
        this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>
  @import "../../../static/login.css";
  .drag_slider{
    margin-top: 0.2rem;
  }
  .code {
    position: relative;
  }
  .code>a {
    display: inline-block;
    width: 1.6rem;
    height: 0.48rem;
    font-size: 0.24rem;
    color: #ff6f00;
    border: 1px solid #ff6f00;
    border-radius: 2px;
    text-align: center;
    line-height: 0.48rem;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .drag_slider {
    width: 100%;
    display: flex;
    margin-top: 0.15rem;
    /*margin-bottom: 0.15rem;*/
    justify-content: center;
  }
</style>



