<template>
  <div class="movebox"  id="slider">
    <div class="movego"></div>
    <div class="txt" id="txt">请按住滑块, 拖动到最右边</div>
    <div class="move moveBefore" v-move id="move"></div>
  </div>
</template>
<script>
  var goOver = false
  var theMove = {}
  export default {
    data() {
      return {

      }
    },
    mounted() {
    },
    methods: {

    },
    directives: {
      move(el,binding, vnode){
        el.ontouchstart = function(e) {
          let X = e.changedTouches[0].clientX - el.offsetLeft;
          el.ontouchmove = function(e) {
            let endx = e.changedTouches[0].clientX - X;
            el.className = 'move moveBefore';
            el.style.left = endx + 'px';
            if(!document.getElementById("slider")){
              return
            }
            let width =  document.getElementById("slider").clientWidth -  document.getElementById("move").clientWidth;
            el.parentNode.children[0].style.width = endx + 'px';
            el.parentNode.children[1].innerHTML = '请按住滑块, 拖动到最右边';
            //临界值小于
            if (endx <= 0) {
              el.style.left = 0 + 'px';
              el.parentNode.children[0].style.width = 0 + 'px'
            }
            //临界值大于
            if (parseInt(el.style.left) >= width) {
              el.style.left = width + 'px';
              el.parentNode.children[0].style.width = width + 'px';
              el.parentNode.children[1].innerHTML = '验证通过';
              el.className = 'move moveSuccess';
              goOver = true
              el.ontouchstart = null;
              vnode.context.$emit('end',true);
            }
          }
        };
        document.ontouchend = function() {
          document.ontouchmove = null
        }
      }
    }
  }
</script>
<style scoped>
  .movebox{
    position: relative;
    background-color: #CCCCCC;
    width: 100%;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    border-radius: 5px;
  }
  .txt{
    position: absolute;
    top: 0;
    width:100%;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
    font-size: .25rem;
    color: #FFFFFF;
    border-radius:4px;
  }
  .movego{
    background-color: #7ac23c;
    height: 100%;
    width: 0;
  }
  .move{
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  .moveBefore{
    background: #fff url("data:image/png;base64,") no-repeat center;
  }
  .moveSuccess{
    background: #fff url("data:image/png;base64,") no-repeat center;
  }
</style>



